import React, { Component, Fragment } from 'react';
import Header from '../header/Header.js';
import Footer from '../footer/Footer.js';
import Product from './Product';
import Filters from './Filters';
import celulares from '../../json/celulares';
import NotFound from '../assets/NotFound';

class App extends Component{

  state = {
    allProducts: celulares,
    productVisible: true,
    activeFilter: [],
    textNotFound: ""
  }

  componentDidMount(){
    this.setState({
      activeFilter: this.state.allProducts
    })
  }

  /* Filtro de orden */

  orderProducts = x =>{
    this.setState({
      activeFilter: x
    })
  }

  /* Filtro de casillas de verificación */

  onFilterChange = (x, y) =>{
    this.setState({
      activeFilter: x,
      productVisible: y
    })
  }

  search = x =>{
    this.setState({
      activeFilter: x,
      productVisible: x
    })
  }

  changeTextNotFound = text => {
    this.setState({
      textNotFound: text
    })
  }

  render(){
    return(
      <Fragment>
        <Header
        onSearch={this.search}
        changeTextNotFound={this.changeTextNotFound}/>
        <div className="container">
          <aside className="edit-products">
            <Filters
            changeElements={this.onFilterChange}
            changeOrder={this.orderProducts}
            onSearch={this.search}
            changeTextNotFound={this.changeTextNotFound}
            products={this.state.activeFilter} />
          </aside>
          <section className="products">
          { this.state.productVisible ? this.state.activeFilter.map( x =>{
            return (
              <Product key={x.id}
              imagen={x.imagen} modelo={x.modelo} stock={x.stock} precio={x.precio} />
              )
            })
            : <NotFound text={this.state.textNotFound} />
          }
          </section>
        </div>
        <Footer />
      </Fragment>
    )
  }
}

export default App;