import React, { Fragment } from 'react';
import './css/filters.css';
import OrderAlphabetical from './OrderAlphabetical.js';
import PrizesFilters from './PrizesFilters';
import CheckboxesFilters from './CheckboxesFilters';

const Filters = props => {
    return(
      <Fragment>
        <OrderAlphabetical changeOrder={props.changeOrder} changeTextNotFound={props.changeTextNotFound} onSearch={props.onSearch} products={props.products} />
        <PrizesFilters />
        <CheckboxesFilters changeElements={props.changeElements} changeTextNotFound={props.changeTextNotFound} />
      </Fragment>
    )
  }

export default Filters;
