import React, {Fragment} from 'react';
import Icon from '../assets/Icon';

const Information = props =>{
  return (
    <Fragment>
    <section id="information">
      <Icon iconName="fas fa-times" iconTitle="Cierra la ventana" id="close" />
      <h5>{props.title}</h5>
      {props.innerHTML}
    </section>
    </Fragment>
  )
}

export default Information;